
import React from 'react';
import 'antd/dist/antd.css';
import './loading.css';
import { Spin, Col , Alert } from 'antd';
import Hp from "../../assets/images/Hp.png";

class Loading extends React.Component {
  render() {
    return (
      <div>
       <Col span={24}>
            <Col span={8}></Col>
            <Col span={9} style={{marginTop:"10%"}}>
                <Spin spinning={!this.props.status} size= "large">
                <Alert
                        message="Loading..."
                        description="Please wait while we load Data."
                        type="info"
                    />
                </Spin>
                {/* <img src={Hp} style={{ width: "24em" }} alt="Hp logo" /> */}
            </Col>
            <Col span={5}></Col>
       </Col>
       <Col span={24} style={{marginTop:'1em'}}>
         <Col span={12}></Col>
         <Col span={9} style={{color:'#b3b3b3',fontWeight:'bold'}}>Loading...</Col>
       </Col>
       </div>
    );
  }
}

export default  Loading;
          