import React from "react";
// import {Icon} from "antd";
import DownloadOutlined from '@ant-design/icons';
import { CSVLink } from "react-csv";
// import { FileTextOutlined } from '@ant-design/icons';
 
class Download extends React.Component{
     constructor(props){
         super(props);
         this.state={
             csvData: props.data
         }
     }
    
     componentWillReceiveProps(nextProps){
         this.setState({
             csvData: nextProps.data
         })
     }
    render(){
        return( this.props.type === "button" ?
             <CSVLink data={this.state.csvData} filename={"Smaple-file.csv"} target="_blank" style={{cursor:"pointer !important"}}>
                 <span style={{color:'#fff'}}>Download</span>
             </CSVLink> :
             <CSVLink data={this.state.csvData} filename={"History.csv"} target="_blank" style={{cursor:"pointer !important"}}>
                <DownloadOutlined />&nbsp;Download
            </CSVLink>)
    }
}
export default Download;