  search = (e) => {
    let data = this.state.backupData;
    let breakException = [];
    let arr1;
    arr1 = data.filter(d =>
      d["baserate"].toString().toLowerCase().indexOf(e.target.value.toLocaleLowerCase()) > -1
    );
    try {
      if (arr1.length === 0) {
        data.some((d) => {
          arr1 = d.children.filter(child =>
            child["baserate"].toString().toLowerCase().indexOf(e.target.value.toLocaleLowerCase()) > -1
          )
          if (arr1.length !== 0) {
            throw breakException;
          }
        })
      }
    }
    catch (e) {
      if (e !== breakException) throw e;
    }

    this.setState({
      tableData: arr1,
      search: e.target.value
    })
  }

