  downloadLink = (e) => {
    let fileName = e.currentTarget.dataset.id.split('.')[0]
    let extension = e.currentTarget.dataset.id.split('.')[1];
    (async () => {
      let blob = await fetch(process.env.REACT_APP_DOMAIN + '/api/v1/downloadAttachment/' + e.currentTarget.dataset.id).then(r => r.blob());
      // 2. Create blob link to download
      const url = window.URL.createObjectURL(new Blob([blob]));
      const link = document.createElement('a');
      link.href = url;
      link.setAttribute('download', fileName + `.` + extension);
      // 3. Append to html page
      document.body.appendChild(link);
      // 4. Force download
      link.click();
      // 5. Clean up and remove the link
      link.parentNode.removeChild(link);
    })();
  }

