  save = () => {
    if (this.state.selectedFile !== '' &&
          this.state.selectedFile !== undefined &&
            this.state.selectedFile !== '') {
      const formData = new FormData();
      let jsonData = {
        dgfRate: this.state.rate,
        note: this.state.notes,
        //createdBy: "Naren"
      };
      // Convert file into binary
      formData.append(
        // "file",new Blob([JSON.stringify(this.state.selectedFile)]),this.state.selectedFile.name
        "file", this.state.selectedFile
      );
      // formData.append("blob", blob, this.state.selectedFile.name);
      formData.append('jsonData', JSON.stringify(jsonData));

      axios.put(process.env.REACT_APP_DOMAIN + '/api/v1/updateDgfRateEntry/' + dgfRateEntryId, formData, {
        headers: { 'Content-Type': 'multipart/form-data' }
      })
        .then((response) => {
          message.success('upload successfully.');
          this.setState({
            edit: false,
            notes: ''
          }, () => this.onRemove())

        })
        .catch((error) => {
          if(error.response)
              message.error(error.response.data.message,5);
        });
    }
    else {
      this.setState({
        message: 'Please upload an attachment while updating the Base rate.'
      })
    }
  }

  onChange = info => {
    const nextState = {};
    switch (info.file.status) {
      case "uploading":
        nextState.selectedFileList = [info.file];
        break;
      case "done":
        nextState.selectedFile = info.file;
        nextState.selectedFileList = [info.file];
        this.setState({
          selectedFile: info.file
        });
        break;

      default:
        // error or removed
        nextState.selectedFile = null;
        nextState.selectedFileList = [];
    }
    this.setState(() => nextState);
  };

