
import React from 'react';
import 'antd/dist/antd.css';
// import './loading.css';
import { Col } from 'antd';
import Hp from "../../assets/images/Hp.png";

class Loading extends React.Component {
    
  componentDidMount(){
    setTimeout(
        function () {
          this.props.history.push('/home');
        }
          .bind(this),
        2000
      );
  }

  render() {
    return (
      <div style={{marginTop:'10em'}}>
       <Col span={24}>
            <Col span={10}></Col>
            <Col span={9}>
                <img src={Hp} style={{ width: "24em"}} alt="Hp logo" />
            </Col>
            <Col span={5}></Col>
       </Col>
       <Col span={24}>
         <Col span={10}></Col>
         {/* <Col span={9} style={{color:'#b3b3b3',fontWeight:'bold',position:'fixed'}}>Loading...</Col> */}
       </Col>
       </div>
    );
  }
}

export default  Loading;
          